:root {
  --background-footer: #2f2f2f;
  --background-main: #e5e5e5;
  --color-red: #fe5f5f;
  --color-red-light: #f1c0c0;
  --color-blue: #3864a7;
  --color-blue-light: #abc6ee;
  --title-color: #2b2b2b;
  --text-color-dark: #000000;
  --text-color-light: #ffffff;
  --color-background-round: #f1f2f2;
  // default
  --spacer: 8px;

  @media screen and (max-width: 995px) {
    --spacer: 7px;
  }
  @media screen and (max-width: 769px) {
    --spacer: 6px;
  }
}
